import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useHistory, useParams } from "react-router-dom";
import { useForm } from "react-hook-form";

const EditApplication = () => {
  let history = useHistory();
  const { id } = useParams();
  const [application, setApplication] = useState({});

  //const [category, setCategory] = useState();
  const { register, handleSubmit, errors } = useForm();

  // const options = [
  //   { value: "medical", label: "Medical" },
  //   { value: "education", label: "Education" },
  //   { value: "repair", label: "Repair & Maintence" },
  //   { value: "other", label: "Other" }
  // ];

  useEffect(() => {
    loadApplication();
  }, []);

  const onSubmit = async (data, e) => {
    e.preventDefault();
    // console.log(data);
    data.json = JSON.stringify(data);
    data.created = new Date();

    // application.json = JSON.stringify(application);
    const headers = { "Content-Type": "application/json" };
    await axios.put(
      `https://testspace03.azurewebsites.net/api/application/${id}`,
      JSON.stringify(data),
      {
        headers: headers,
      }
    );
    history.push("/Listapplication");
  };

  const loadApplication = async () => {
    const result = await axios.get(
      `https://testspace03.azurewebsites.net/api/application/${id}`
    );
    setApplication(JSON.parse(result.data.json));
    // console.log(JSON.parse(result.data.json));
  };

  return (
    <div className="container py-2">
      <div>
        <div class="row">
          <div class="col-xs-4 col-md-4 col-sm-4">
            <Link
              className="btn btn-primary btn-sm"
              to="/ListApplications"
              onClick={() => {
                history.goBack();
              }}
            >
              Back
            </Link>
          </div>
          <div class="col-xs-4 col-md-4 col-sm-4">
            <h6>Edit Application</h6>
          </div>
          <div class="col-xs-4 col-md-4 col-sm-4">
            <div class="row py-2">
              <div class="col-2"></div>
            </div>
          </div>
        </div>
      </div>

      <div className="mx-auto shadow p-2">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                File Code + Status
              </span>
            </div>
            <input
              name="filecode"
              defaultValue={application.filecode}
              type="text"
              class="form-control"
              ref={register({ required: true, maxLength: 12 })}
            ></input>
            <input
              name="status"
              defaultValue={application.status}
              ref={register({ required: true })}
              type="text"
              class="form-control"
            ></input>
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Name + Age
              </span>
            </div>
            <input
              name="name"
              defaultValue={application.name}
              ref={register({ required: true, maxLength: 50, minLength: 5 })}
              type="text"
              class="form-control"
            ></input>
            <input
              name="age"
              defaultValue={application.age}
              ref={register({ min: 18, max: 99 })}
              type="text"
              class="form-control"
            ></input>
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Address
              </span>
            </div>
            <input
              name="address"
              defaultValue={application.address}
              ref={register({ required: true, maxLength: 100, minLength: 5 })}
              type="text"
              class="form-control"
            ></input>
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Phone + Email
              </span>
            </div>
            <input
              name="phone"
              defaultValue={application.phone}
              ref={register({ required: false, maxLength: 11, minLength: 8 })}
              type="text"
              class="form-control"
            ></input>
            <input
              name="email"
              defaultValue={application.email}
              ref={register({
                required: false,
                pattern: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
              })}
              type="text"
              class="form-control"
            ></input>
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Category + Amount
              </span>
            </div>

            <select
              ref={register({ required: true })}
              name="category"
              class="form-control"
              placeholder="Category"
            >
              <option value="Medical">Medical</option>
              <option value="Education">Education</option>
              <option value="House Repair">House Repair</option>
              <option value="Other">Other</option>
            </select>

            <input
              name="amount"
              defaultValue={application.amount}
              ref={register({ required: false, maxLength: 6, minLength: 2 })}
              type="text"
              class="form-control"
            ></input>
          </div>

          <div class="input-group input-group-sm mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="">
                Comment/Action
              </span>
            </div>
            <textarea
              name="comment"
              defaultValue={application.comment}
              ref={register}
              rows="5"
              class="form-control"
            ></textarea>
          </div>

          <button className="btn btn-warning btn-block">
            Update Application
          </button>
        </form>
      </div>
    </div>
  );
};

export default EditApplication;
